import styled from '@emotion/styled';
import { FC } from 'react';

type SliceSectionProps = {
    imgUrl: string;
    isEven: boolean;
};

const StyledSection = styled.section({
    height: '50vh',
    maxHeight: 400,
    display: 'flex',
    width: '100%',
    position: 'relative',
    margin: '50px 0',
    borderRadius: 10,
    overflow: 'hidden',
    boxShadow: 'var(--light-green) 0px 0px 25px',
});

const BackgroundImage = styled.img<{ isEven: boolean }>(({ isEven }) => {
    return {
        position: 'absolute',
        width: '68%',
        height: '100%',
        objectFit: 'cover',

        ...(isEven && {
            right: 0,
        }),

        '@media screen and (max-width:450px)': {
            left: 0,

            ...(isEven && {
                bottom: 0,
            }),

            ...(!isEven && {
                top: 0,
            }),



            width: '100%',
            height: '70%',
        },

        '@media screen and (min-width:820px)': {
            width: '60%',
        },
    }

});

const ContentContainer = styled.div<{ isEven: boolean }>(({ isEven }) => {
    return ({
        width: '100%',
        height: '100%',
        position: 'absolute',
        color: '#000',
        display: 'flex',
        justifyContent: isEven ? 'flex-start' : 'flex-end',
        alignItems: 'center',
        background: isEven ? 'linear-gradient(-70deg, transparent 50%, var(--light-green) 50%, white 50.5%)' : 'linear-gradient(-70deg, white 49.5%, var(--light-green) 50%, transparent 50%)',
        textAlign: 'center',

        '@media screen and (max-width: 450px)': {
            flexDirection: 'column',
            background: isEven ? 'linear-gradient(-20deg, transparent 50%, var(--light-green) 50%, white 51%)' : 'linear-gradient(-20deg, white 49%, var(--light-green) 50%, transparent 50%)',
        }
    });

});

const TextContainer = styled.div({
    width: '50%',
    height: '50%',
    textAlign: 'center',
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',

    '& > *': {
        margin: '5px 5%',
        maxWidth: '40ch',
    }
});

export const SlicedSection: FC<SliceSectionProps> = ({ imgUrl, isEven, children }) => {
    return (
        <StyledSection>
            <BackgroundImage isEven={isEven} src={imgUrl} />
            <ContentContainer isEven={isEven} >
                <TextContainer>
                    {children}
                </TextContainer>
            </ContentContainer>
        </StyledSection>
    )
}