import { FC, useRef, useState } from "react";
import styled from "@emotion/styled";
import Cookies from 'universal-cookie';

import { firebase } from '@firebase/app';
import '@firebase/firestore';
import '@firebase/analytics';

type RSVPModalProps = {
    isActive: boolean;
    setIsActive: (newValue: boolean) => void;
    setRsvpSent: (newValue: boolean) => void;
}


const ModalBackground = styled.div<{ isActive: boolean }>(({ isActive }) => {
    return {
        position: 'fixed',
        width: '100vw',
        height: '100vh',
        inset: 0,
        zIndex: 100,
        background: 'rgba(255, 255, 255, 80%)',
        display: isActive ? 'flex' : 'none',
        justifyContent: 'center',
        alignItems: 'center',
    }
});

const ModalContainer = styled.div({
    position: 'relative',
    display: 'flex',
    flexDirection: 'column',
    width: 300,
    maxWidth: 'calc(100vw - 20px)',
    background: '#FFF',
    padding: 50,
    borderRadius: 10,
    overflow: 'hidden',
    boxShadow: 'rgba(0, 0, 0, 0.35) 0px 5px 15px',
    fontSize: '1.5em',

    '& > label': {
        display: 'flex',
        flexDirection: 'column',
        marginTop: 30,
        marginBottom: 30,
        textAlign: 'center',
        fontWeight: 600,

        '&:focus-within': {
            background: 'var(--complimentary-gradient)',
            backgroundClip: 'text',
            WebkitTextFillColor: 'transparent',

            '& > input': {
                WebkitTextFillColor: '#000',
            },
        },
    },

    '& > * > input': {
        height: 30,
        fontSize: 25,
        padding: 10,
        borderRadius: 3,
        border: '2px solid #9e9e9e',

        '&:focus-visible': {
            outline: 'none',
            borderColor: 'var(--light-green)',
        }
    },
});

const ButtonWrapper = styled.div({
});

const SubmitButton = styled.div({
    background: '#FFF',
    borderRadius: 0,
    border: '4px solid #000',
    textTransform: 'uppercase',
    width: 'max-content',
    margin: '30px auto 0px',
    padding: 15,
    fontWeight: 600,
    cursor: 'pointer',
    userSelect: 'none',

    '&:hover': {
        transform: 'scale(1.02)',
    },

    '&:active': {
        transform: 'scale(0.98)',
    },
});

const CloseButton = styled.img({
    position: 'absolute',
    top: 0,
    left: 0,
    padding: 20,
    width: 25,
    height: 25,
    cursor: 'pointer',
});

var firebaseConfig = {
    apiKey: "AIzaSyCe0U5BmYkQ7i4Vkiktrd9aCQBU3KcMSMw",
    authDomain: "ellis-birthday.firebaseapp.com",
    projectId: "ellis-birthday",
    storageBucket: "ellis-birthday.appspot.com",
    messagingSenderId: "383973844651",
    appId: "1:383973844651:web:5fa4d1b7d80fe275ee39ee",
    measurementId: "G-VH95N980EW"
};

export const RSVPModal: FC<RSVPModalProps> = ({ isActive, setIsActive, setRsvpSent }) => {
    const cookies = new Cookies();
    const modalContainer = useRef<HTMLDivElement>(null);

    const [name, setName] = useState<string>('');
    const [number, setNumber] = useState<number>(1);

    if (!firebase.apps.length) {
        firebase.initializeApp(firebaseConfig);
    } else {
        firebase.app(); // if already initialized, use that one
    }
    firebase.analytics();

    const db = firebase.firestore();

    const closeFromBackground = (e: any) => {
        if (modalContainer.current && !modalContainer.current.contains(e.target)) {
            setIsActive(false);
        }
    }

    const closeModal = () => {
        setIsActive(false);
    }

    const submitForm = async (isComming: boolean) => {

        await db.collection("Guests").doc(name).set({
            number,
            isComming,
        })

        if(isComming) {
            await db.collection("Total_Guests").doc('Total').update({
                total: firebase.firestore.FieldValue.increment(number)
            })
        }

        cookies.set('rsvpSent', true);
        setIsActive(false);
        setRsvpSent(true);
    };

    //TODO: Disable when sent preferably with a cookie (possibly also a state)
    return (
        <ModalBackground isActive={isActive} onClick={(e) => { closeFromBackground(e) }}>
            <ModalContainer ref={modalContainer}>
                <CloseButton src='/close.png' alt='close-button' onClick={closeModal} />
                <label htmlFor="Name">
                    Namn
                    <input type="text" name="Name" id="name" onChange={(e: any) => { setName(e.target.value); }} />
                </label>
                <label htmlFor="number">
                    Hur många är ni
                    <input type="number" name="Number" id="number" onChange={(e: any) => { setNumber(e.target.value); }} placeholder={number + ''} inputMode='numeric' />
                </label>
                <ButtonWrapper>
                    <SubmitButton onClick={() => { submitForm(true) }}>Vi kommer</SubmitButton>
                    <SubmitButton onClick={() => { submitForm(false) }}>Vi kommer inte</SubmitButton>
                </ButtonWrapper>
            </ModalContainer>
        </ModalBackground>
    );
};