import styled from "@emotion/styled";
import axios from "axios";
import Cookies from 'universal-cookie';
import { ReactElement, useEffect, useState } from "react";
import { HeroSection } from "./components/hero-section";
import { RSVPModal } from "./components/rsvp-modal";
import { SlicedSection } from "./components/sliced-section";

type Card = {
  imgUrl: string;
  heading: string;
  text: string;
}

const AppContainer = styled.div<{modalActive: boolean}>(({ modalActive }) => {

  return {
    fontFamily: "American Typewriter, sans-serif",

    ...(modalActive && {
      height: '100vh',
      width: '100vw',
      overflow: 'hidden',
    })
  }
});

const PageContainer = styled.div({
  margin: "0 auto",
  width: 1100,
  maxWidth: '90vw',
});

function App() {
  const cookies = new Cookies();
  const [data, setData] = useState([]);
  const [modalActive, setModalActive] = useState(false);
  const [rsvpSent, setRsvpSent] = useState(cookies.get('rsvpSent'));


  useEffect(() => {
    axios.get('/cards.json').then(response => {
      setData(response.data.cards);
    })
  }, []);

  let cardItems: ReactElement[] = [];
  if (data) {
    data.forEach((card: Card, index) => {
      cardItems.push(<SlicedSection imgUrl={card.imgUrl} isEven={index % 2 === 0} key={`card-${index}`}>
        <h2>{card.heading}</h2>
        <p>{card.text}</p>
      </SlicedSection>)
    });
  }
  return (
    <AppContainer modalActive={modalActive}>
      <HeroSection setModalActive={setModalActive} rsvpSent={rsvpSent} />
      <PageContainer>
        {cardItems}
      </PageContainer>
      <RSVPModal isActive={modalActive} setIsActive={setModalActive} setRsvpSent={setRsvpSent}/>
    </AppContainer>
  );
}

export default App;
